@use "./base/var.scss";

.video {
  padding: 0 20px;
}

.video__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 39px;
}

.video__title {
  text-align: center;
  margin-bottom: 27px;
}

.video__description {
  text-align: center;

  // paragraph overflow
  &:not(:last-child) {
    margin-bottom: 15px;
  }
}

.video__video-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.video__img {
  width: 100%;
  max-width: 800px;
}

.video__play-button {
  position: absolute;
  cursor: pointer;
  content: "";
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  transition: all 0.3s ease;
  width: 48px;
  height: 54px;
  opacity: 0.7;
  border: none;
  background: transparent url("../../img/icons/video-play-icon.svg") no-repeat 50% 50%;

  &:hover {
    opacity: 1;
  }
}


@media (min-width: 640px) {
  .video {
    padding: 0 60px;
  }

  .video__text-wrapper {
    margin-bottom: 89px;
  }

  .video__title {
    font-size: 30px;
  }

  .video__description {
    max-width: 445px;
  }
}

